"use client";

import React from "react"

import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Progress } from "@/components/ui/progress";
import { Badge } from "@/components/ui/badge";
import {
  TrendingUp,
  Users,
  Target,
  AlertTriangle,
  CheckCircle,
  DollarSign,
  Lightbulb,
  BarChart3,
  Shield,
  Rocket,
} from "lucide-react";
import type { AnalysisResult } from "@/lib/analysis-types";

interface AnalysisResultProps {
  result: AnalysisResult;
}

function ScoreCard({
  title,
  score,
  icon: Icon,
  description,
}: {
  title: string;
  score: number;
  icon: React.ElementType;
  description: string;
}) {
  const getScoreColor = (score: number) => {
    if (score >= 80) return "text-chart-1";
    if (score >= 60) return "text-chart-3";
    if (score >= 40) return "text-chart-4";
    return "text-destructive";
  };

  const getProgressColor = (score: number) => {
    if (score >= 80) return "bg-chart-1";
    if (score >= 60) return "bg-chart-3";
    if (score >= 40) return "bg-chart-4";
    return "bg-destructive";
  };

  return (
    <Card className="bg-card border-border">
      <CardContent className="p-4">
        <div className="flex items-center gap-3 mb-3">
          <div className="p-2 rounded-lg bg-secondary">
            <Icon className="h-5 w-5 text-accent" />
          </div>
          <div className="flex-1">
            <h3 className="font-medium text-foreground">{title}</h3>
            <p className={`text-2xl font-bold ${getScoreColor(score)}`}>
              {score}점
            </p>
          </div>
        </div>
        <Progress
          value={score}
          className={`h-2 mb-2 [&>div]:${getProgressColor(score)}`}
        />
        <p className="text-sm text-muted-foreground">{description}</p>
      </CardContent>
    </Card>
  );
}

export function AnalysisResultView({ result }: AnalysisResultProps) {
  const getOverallGrade = (score: number) => {
    if (score >= 80) return { grade: "A", label: "매우 유망", color: "bg-chart-1" };
    if (score >= 70) return { grade: "B+", label: "유망", color: "bg-chart-1" };
    if (score >= 60) return { grade: "B", label: "가능성 있음", color: "bg-chart-3" };
    if (score >= 50) return { grade: "C+", label: "보완 필요", color: "bg-chart-4" };
    if (score >= 40) return { grade: "C", label: "재검토 필요", color: "bg-chart-4" };
    return { grade: "D", label: "높은 리스크", color: "bg-destructive" };
  };

  const overallGrade = getOverallGrade(result.overallScore);

  return (
    <div className="space-y-6">
      {/* Overall Score */}
      <Card className="bg-card border-border overflow-hidden">
        <div className="relative">
          <div className="absolute inset-0 bg-gradient-to-r from-accent/20 to-transparent" />
          <CardHeader className="relative">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm text-muted-foreground mb-1">종합 사업성 평가</p>
                <CardTitle className="text-3xl text-foreground">
                  {result.ideaSummary}
                </CardTitle>
              </div>
              <div className="text-center">
                <div
                  className={`w-20 h-20 rounded-full ${overallGrade.color} flex items-center justify-center`}
                >
                  <span className="text-3xl font-bold text-foreground">
                    {overallGrade.grade}
                  </span>
                </div>
                <p className="text-sm text-muted-foreground mt-2">
                  {overallGrade.label}
                </p>
                <p className="text-lg font-semibold text-foreground">
                  {result.overallScore}점/100점
                </p>
              </div>
            </div>
          </CardHeader>
        </div>
      </Card>

      {/* Score Cards Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
        <ScoreCard
          title="시장성"
          score={result.scores.marketPotential}
          icon={TrendingUp}
          description={result.scoreDescriptions.marketPotential}
        />
        <ScoreCard
          title="경쟁 환경"
          score={result.scores.competition}
          icon={Target}
          description={result.scoreDescriptions.competition}
        />
        <ScoreCard
          title="수익성"
          score={result.scores.profitability}
          icon={DollarSign}
          description={result.scoreDescriptions.profitability}
        />
        <ScoreCard
          title="실행 가능성"
          score={result.scores.feasibility}
          icon={Rocket}
          description={result.scoreDescriptions.feasibility}
        />
        <ScoreCard
          title="리스크"
          score={result.scores.riskLevel}
          icon={Shield}
          description={result.scoreDescriptions.riskLevel}
        />
        <ScoreCard
          title="고객 니즈"
          score={result.scores.customerNeed}
          icon={Users}
          description={result.scoreDescriptions.customerNeed}
        />
      </div>

      {/* Market Analysis */}
      <Card className="bg-card border-border">
        <CardHeader>
          <CardTitle className="flex items-center gap-2 text-foreground">
            <BarChart3 className="h-5 w-5 text-accent" />
            시장 분석
          </CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <div className="p-4 bg-secondary rounded-lg">
              <p className="text-sm text-muted-foreground">TAM (전체 시장)</p>
              <p className="text-xl font-bold text-foreground">{result.marketAnalysis.tam}</p>
            </div>
            <div className="p-4 bg-secondary rounded-lg">
              <p className="text-sm text-muted-foreground">SAM (유효 시장)</p>
              <p className="text-xl font-bold text-foreground">{result.marketAnalysis.sam}</p>
            </div>
            <div className="p-4 bg-secondary rounded-lg">
              <p className="text-sm text-muted-foreground">SOM (목표 시장)</p>
              <p className="text-xl font-bold text-foreground">{result.marketAnalysis.som}</p>
            </div>
          </div>
          <p className="text-muted-foreground">{result.marketAnalysis.summary}</p>
        </CardContent>
      </Card>

      {/* Competitors */}
      <Card className="bg-card border-border">
        <CardHeader>
          <CardTitle className="flex items-center gap-2 text-foreground">
            <Target className="h-5 w-5 text-accent" />
            경쟁 분석
          </CardTitle>
        </CardHeader>
        <CardContent>
          <div className="space-y-3">
            {result.competitors.map((competitor, index) => (
              <div
                key={index}
                className="flex items-center justify-between p-3 bg-secondary rounded-lg"
              >
                <div>
                  <p className="font-medium text-foreground">{competitor.name}</p>
                  <p className="text-sm text-muted-foreground">
                    {competitor.description}
                  </p>
                </div>
                <Badge variant="outline" className="border-border text-muted-foreground">
                  {competitor.threat}
                </Badge>
              </div>
            ))}
          </div>
          <p className="mt-4 text-muted-foreground">{result.competitionSummary}</p>
        </CardContent>
      </Card>

      {/* Strengths & Weaknesses */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <Card className="bg-card border-border">
          <CardHeader>
            <CardTitle className="flex items-center gap-2 text-foreground">
              <CheckCircle className="h-5 w-5 text-chart-1" />
              강점
            </CardTitle>
          </CardHeader>
          <CardContent>
            <ul className="space-y-2">
              {result.strengths.map((strength, index) => (
                <li key={index} className="flex items-start gap-2">
                  <CheckCircle className="h-4 w-4 text-chart-1 mt-0.5 shrink-0" />
                  <span className="text-muted-foreground">{strength}</span>
                </li>
              ))}
            </ul>
          </CardContent>
        </Card>

        <Card className="bg-card border-border">
          <CardHeader>
            <CardTitle className="flex items-center gap-2 text-foreground">
              <AlertTriangle className="h-5 w-5 text-chart-3" />
              약점 및 리스크
            </CardTitle>
          </CardHeader>
          <CardContent>
            <ul className="space-y-2">
              {result.weaknesses.map((weakness, index) => (
                <li key={index} className="flex items-start gap-2">
                  <AlertTriangle className="h-4 w-4 text-chart-3 mt-0.5 shrink-0" />
                  <span className="text-muted-foreground">{weakness}</span>
                </li>
              ))}
            </ul>
          </CardContent>
        </Card>
      </div>

      {/* Recommendations */}
      <Card className="bg-card border-border">
        <CardHeader>
          <CardTitle className="flex items-center gap-2 text-foreground">
            <Lightbulb className="h-5 w-5 text-accent" />
            실행 권장사항
          </CardTitle>
        </CardHeader>
        <CardContent>
          <div className="space-y-3">
            {result.recommendations.map((rec, index) => (
              <div
                key={index}
                className="flex items-start gap-3 p-3 bg-secondary rounded-lg"
              >
                <div className="w-6 h-6 rounded-full bg-accent/20 flex items-center justify-center shrink-0">
                  <span className="text-sm font-medium text-accent">
                    {index + 1}
                  </span>
                </div>
                <div>
                  <p className="font-medium text-foreground">{rec.title}</p>
                  <p className="text-sm text-muted-foreground">{rec.description}</p>
                </div>
              </div>
            ))}
          </div>
        </CardContent>
      </Card>

      {/* Conclusion */}
      <Card className="bg-card border-border">
        <CardHeader>
          <CardTitle className="text-foreground">종합 결론</CardTitle>
        </CardHeader>
        <CardContent>
          <p className="text-muted-foreground leading-relaxed">{result.conclusion}</p>
        </CardContent>
      </Card>
    </div>
  );
}
