"use client";

import React from "react"

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Textarea } from "@/components/ui/textarea";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { ArrowRight, Sparkles } from "lucide-react";

interface IdeaInputFormProps {
  onSubmit: (data: BusinessIdeaInput) => void;
  isLoading: boolean;
}

export interface BusinessIdeaInput {
  ideaDescription: string;
  problemSolved: string;
  targetCustomer: string;
  industry: string;
  revenueModel: string;
  competitiveAdvantage: string;
}

const industries = [
  { value: "tech", label: "IT/테크" },
  { value: "ecommerce", label: "이커머스/리테일" },
  { value: "healthcare", label: "헬스케어/의료" },
  { value: "fintech", label: "핀테크/금융" },
  { value: "education", label: "교육/에듀테크" },
  { value: "food", label: "식품/F&B" },
  { value: "entertainment", label: "엔터테인먼트/미디어" },
  { value: "manufacturing", label: "제조업" },
  { value: "service", label: "서비스업" },
  { value: "other", label: "기타" },
];

const revenueModels = [
  { value: "subscription", label: "구독 모델" },
  { value: "transaction", label: "거래 수수료" },
  { value: "advertising", label: "광고 기반" },
  { value: "saas", label: "SaaS" },
  { value: "marketplace", label: "마켓플레이스" },
  { value: "freemium", label: "프리미엄" },
  { value: "direct_sales", label: "직접 판매" },
  { value: "licensing", label: "라이센싱" },
  { value: "other", label: "기타/미정" },
];

export function IdeaInputForm({ onSubmit, isLoading }: IdeaInputFormProps) {
  const [formData, setFormData] = useState<BusinessIdeaInput>({
    ideaDescription: "",
    problemSolved: "",
    targetCustomer: "",
    industry: "",
    revenueModel: "",
    competitiveAdvantage: "",
  });

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    onSubmit(formData);
  };

  const isFormValid =
    formData.ideaDescription.trim() !== "" &&
    formData.problemSolved.trim() !== "" &&
    formData.targetCustomer.trim() !== "" &&
    formData.industry !== "";

  return (
    <form onSubmit={handleSubmit} className="space-y-6">
      <div className="space-y-2">
        <Label htmlFor="ideaDescription" className="text-foreground">
          사업 아이디어 설명 <span className="text-accent">*</span>
        </Label>
        <Textarea
          id="ideaDescription"
          placeholder="당신의 사업 아이디어를 자세히 설명해주세요. 예: AI 기반 영어 회화 튜터링 앱으로, 사용자의 실력에 맞춰 실시간 피드백을 제공합니다."
          className="min-h-[120px] bg-input border-border text-foreground placeholder:text-muted-foreground resize-none"
          value={formData.ideaDescription}
          onChange={(e) =>
            setFormData({ ...formData, ideaDescription: e.target.value })
          }
          disabled={isLoading}
        />
      </div>

      <div className="space-y-2">
        <Label htmlFor="problemSolved" className="text-foreground">
          해결하려는 문제 <span className="text-accent">*</span>
        </Label>
        <Textarea
          id="problemSolved"
          placeholder="이 사업이 해결하려는 고객의 문제나 페인포인트는 무엇인가요?"
          className="min-h-[80px] bg-input border-border text-foreground placeholder:text-muted-foreground resize-none"
          value={formData.problemSolved}
          onChange={(e) =>
            setFormData({ ...formData, problemSolved: e.target.value })
          }
          disabled={isLoading}
        />
      </div>

      <div className="space-y-2">
        <Label htmlFor="targetCustomer" className="text-foreground">
          타겟 고객 <span className="text-accent">*</span>
        </Label>
        <Input
          id="targetCustomer"
          placeholder="예: 20-30대 직장인, 영어 회화 실력을 향상시키고 싶은 사람들"
          className="bg-input border-border text-foreground placeholder:text-muted-foreground"
          value={formData.targetCustomer}
          onChange={(e) =>
            setFormData({ ...formData, targetCustomer: e.target.value })
          }
          disabled={isLoading}
        />
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <div className="space-y-2">
          <Label htmlFor="industry" className="text-foreground">
            산업 분야 <span className="text-accent">*</span>
          </Label>
          <Select
            value={formData.industry}
            onValueChange={(value) =>
              setFormData({ ...formData, industry: value })
            }
            disabled={isLoading}
          >
            <SelectTrigger className="bg-input border-border text-foreground">
              <SelectValue placeholder="산업 분야 선택" />
            </SelectTrigger>
            <SelectContent>
              {industries.map((industry) => (
                <SelectItem key={industry.value} value={industry.value}>
                  {industry.label}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>

        <div className="space-y-2">
          <Label htmlFor="revenueModel" className="text-foreground">
            수익 모델
          </Label>
          <Select
            value={formData.revenueModel}
            onValueChange={(value) =>
              setFormData({ ...formData, revenueModel: value })
            }
            disabled={isLoading}
          >
            <SelectTrigger className="bg-input border-border text-foreground">
              <SelectValue placeholder="수익 모델 선택" />
            </SelectTrigger>
            <SelectContent>
              {revenueModels.map((model) => (
                <SelectItem key={model.value} value={model.value}>
                  {model.label}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>
      </div>

      <div className="space-y-2">
        <Label htmlFor="competitiveAdvantage" className="text-foreground">
          경쟁 우위 / 차별화 포인트
        </Label>
        <Textarea
          id="competitiveAdvantage"
          placeholder="경쟁사 대비 어떤 차별화된 가치를 제공하나요? (선택사항)"
          className="min-h-[80px] bg-input border-border text-foreground placeholder:text-muted-foreground resize-none"
          value={formData.competitiveAdvantage}
          onChange={(e) =>
            setFormData({ ...formData, competitiveAdvantage: e.target.value })
          }
          disabled={isLoading}
        />
      </div>

      <Button
        type="submit"
        className="w-full bg-primary text-primary-foreground hover:bg-primary/90 h-12 text-base font-medium"
        disabled={!isFormValid || isLoading}
      >
        {isLoading ? (
          <>
            <Sparkles className="mr-2 h-5 w-5 animate-pulse" />
            AI가 분석 중입니다...
          </>
        ) : (
          <>
            사업 타당성 분석하기
            <ArrowRight className="ml-2 h-5 w-5" />
          </>
        )}
      </Button>
    </form>
  );
}
