"use client";

import React from "react"

import { useState } from "react";
import {
  IdeaInputForm,
  type BusinessIdeaInput,
} from "@/components/idea-input-form";
import { AnalysisResultView } from "@/components/analysis-result";
import type { AnalysisResult } from "@/lib/analysis-types";
import { Sparkles, RotateCcw, Brain, Target, TrendingUp, Shield } from "lucide-react";
import { Button } from "@/components/ui/button";

export default function Home() {
  const [isLoading, setIsLoading] = useState(false);
  const [result, setResult] = useState<AnalysisResult | null>(null);
  const [error, setError] = useState<string | null>(null);

  const handleSubmit = async (data: BusinessIdeaInput) => {
    setIsLoading(true);
    setError(null);

    try {
      const response = await fetch("/api/analyze", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(data),
      });

      if (!response.ok) {
        throw new Error("분석 중 오류가 발생했습니다.");
      }

      const analysisResult = await response.json();
      setResult(analysisResult);
    } catch (err) {
      setError(err instanceof Error ? err.message : "알 수 없는 오류가 발생했습니다.");
    } finally {
      setIsLoading(false);
    }
  };

  const handleReset = () => {
    setResult(null);
    setError(null);
  };

  return (
    <main className="min-h-screen bg-background">
      {/* Header */}
      <header className="border-b border-border">
        <div className="max-w-6xl mx-auto px-4 py-4 flex items-center justify-between">
          <div className="flex items-center gap-2">
            <div className="p-2 rounded-lg bg-accent/20">
              <Sparkles className="h-6 w-6 text-accent" />
            </div>
            <span className="text-xl font-bold text-foreground">BizAnalyzer AI</span>
          </div>
          {result && (
            <Button
              variant="outline"
              onClick={handleReset}
              className="border-border text-muted-foreground hover:text-foreground bg-transparent"
            >
              <RotateCcw className="mr-2 h-4 w-4" />
              새로운 분석
            </Button>
          )}
        </div>
      </header>

      <div className="max-w-6xl mx-auto px-4 py-8">
        {!result ? (
          <div className="space-y-8">
            {/* Hero Section */}
            <div className="text-center space-y-4 py-8">
              <h1 className="text-4xl md:text-5xl font-bold text-foreground text-balance">
                AI 기반 사업 아이디어
                <br />
                <span className="text-accent">타당성 분석</span>
              </h1>
              <p className="text-lg text-muted-foreground max-w-2xl mx-auto text-pretty">
                사업 아이디어를 입력하면 AI가 시장성, 경쟁 환경, 수익성, 리스크를
                종합적으로 분석해드립니다.
              </p>
            </div>

            {/* Features */}
            <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-8">
              <FeatureCard
                icon={Brain}
                title="AI 분석"
                description="GPT 기반 심층 분석"
              />
              <FeatureCard
                icon={TrendingUp}
                title="시장성 평가"
                description="TAM/SAM/SOM 추정"
              />
              <FeatureCard
                icon={Target}
                title="경쟁 분석"
                description="경쟁사 매핑 및 위협 분석"
              />
              <FeatureCard
                icon={Shield}
                title="리스크 진단"
                description="잠재적 위험 요소 도출"
              />
            </div>

            {/* Form */}
            <div className="max-w-2xl mx-auto">
              <div className="bg-card border border-border rounded-xl p-6 md:p-8">
                <h2 className="text-xl font-semibold text-foreground mb-6">
                  사업 아이디어 입력
                </h2>
                <IdeaInputForm onSubmit={handleSubmit} isLoading={isLoading} />
              </div>
            </div>

            {error && (
              <div className="max-w-2xl mx-auto">
                <div className="bg-destructive/10 border border-destructive/20 rounded-lg p-4 text-center">
                  <p className="text-destructive">{error}</p>
                </div>
              </div>
            )}
          </div>
        ) : (
          <div className="space-y-6">
            <div className="flex items-center gap-2 text-muted-foreground">
              <Sparkles className="h-5 w-5 text-accent" />
              <span>AI 분석 완료</span>
            </div>
            <AnalysisResultView result={result} />
          </div>
        )}
      </div>

      {/* Footer */}
      <footer className="border-t border-border mt-16">
        <div className="max-w-6xl mx-auto px-4 py-6 text-center text-sm text-muted-foreground">
          <p>
            본 분석 결과는 AI가 생성한 참고 자료이며, 실제 투자 및 사업 결정에는 전문가
            상담을 권장합니다.
          </p>
        </div>
      </footer>
    </main>
  );
}

function FeatureCard({
  icon: Icon,
  title,
  description,
}: {
  icon: React.ElementType;
  title: string;
  description: string;
}) {
  return (
    <div className="p-4 bg-card border border-border rounded-lg text-center">
      <div className="inline-flex p-2 rounded-lg bg-secondary mb-3">
        <Icon className="h-5 w-5 text-accent" />
      </div>
      <h3 className="font-medium text-foreground text-sm">{title}</h3>
      <p className="text-xs text-muted-foreground mt-1">{description}</p>
    </div>
  );
}
